import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useToast } from '@/hooks/use-toast';
import { db } from '@/firebase';
import { collection, addDoc, query, where, getDocs, serverTimestamp, orderBy, limit } from 'firebase/firestore';
import { ChevronDown, ChevronUp } from 'lucide-react';

interface SimCardsAccessDialogProps {
  isOpen: boolean;
  onClose: () => void;
  userId: string;
  userEmail: string;
}

export const SimCardsAccessDialog: React.FC<SimCardsAccessDialogProps> = ({
  isOpen,
  onClose,
  userId,
  userEmail
}) => {
  const [step, setStep] = useState<'initial' | 'subscribe' | 'pending'>('initial');
  const [selectedPlan, setSelectedPlan] = useState<string>('');
  const [showPrices, setShowPrices] = useState(false);
  const [loading, setLoading] = useState(false);
  const { toast } = useToast();

  useEffect(() => {
    if (isOpen) {
      checkPendingRequests();
    }
  }, [isOpen, userId]);

  const checkPendingRequests = async () => {
    setLoading(true);
    try {
      const q = query(
        collection(db, 'simCardRequests'),
        where('userId', '==', userId),
        where('status', '==', 'pending'),
        orderBy('createdAt', 'desc'),
        limit(1)
      );
      const snapshot = await getDocs(q);
      if (!snapshot.empty) {
        setStep('pending');
      } else {
        setStep('initial');
      }
    } catch (error) {
      console.error('Error checking requests:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubscribe = async () => {
    if (!selectedPlan) {
      toast({
        title: "خطأ",
        description: "الرجاء اختيار باقة الاشتراك",
        variant: "destructive",
      });
      return;
    }

    setLoading(true);
    try {
      await addDoc(collection(db, 'simCardRequests'), {
        userId,
        email: userEmail || 'No Email',
        plan: selectedPlan,
        status: 'pending',
        createdAt: serverTimestamp(),
      });
      
      setStep('pending');
      toast({
        title: "تم الإرسال",
        description: "تم إرسال طلب الاشتراك بنجاح",
      });
    } catch (error) {
      console.error('Error sending request:', error);
      toast({
        title: "خطأ",
        description: "حدث خطأ أثناء إرسال الطلب",
        variant: "destructive",
      });
    } finally {
      setLoading(false);
    }
  };

  const renderContent = () => {
    if (loading) {
      return <div className="py-8 text-center">جاري التحميل...</div>;
    }

    switch (step) {
      case 'pending':
        return (
          <div className="space-y-4 py-4 text-center">
            <div className="text-yellow-600 bg-yellow-50 p-4 rounded-lg">
              <p className="font-semibold">طلبك قيد المراجعة</p>
              <p className="text-sm mt-2">لقد قمت بإرسال طلب اشتراك بالفعل. سيتم تفعيله قريباً.</p>
            </div>
            <Button onClick={onClose} variant="outline" className="w-full">
              إغلاق
            </Button>
          </div>
        );

      case 'subscribe':
        return (
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <label className="text-sm font-medium">اختر باقة الاشتراك:</label>
              <Select value={selectedPlan} onValueChange={setSelectedPlan}>
                <SelectTrigger>
                  <SelectValue placeholder="اختر الباقة" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="monthly">شهرية</SelectItem>
                  <SelectItem value="quarterly">ربع سنوية (3 شهور)</SelectItem>
                  <SelectItem value="yearly">سنوية</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="border rounded-lg overflow-hidden">
              <button
                type="button"
                onClick={() => setShowPrices(!showPrices)}
                className="w-full flex items-center justify-between p-3 bg-gray-50 hover:bg-gray-100 transition-colors"
              >
                <span className="text-sm font-medium text-gray-700">أسعار الاشتراك</span>
                {showPrices ? <ChevronUp className="h-4 w-4 text-gray-500" /> : <ChevronDown className="h-4 w-4 text-gray-500" />}
              </button>
              
              {showPrices && (
                <div className="p-3 bg-white border-t space-y-2 text-sm animate-in slide-in-from-top-1">
                  <div className="flex justify-between items-center border-b border-gray-100 pb-2 last:border-0 last:pb-0">
                    <span className="text-gray-600">الباقة الشهرية</span>
                    <span className="font-semibold text-gray-900">35 ج.م</span>
                  </div>
                  <div className="flex justify-between items-center border-b border-gray-100 pb-2 last:border-0 last:pb-0">
                    <span className="text-gray-600">الباقة الربع سنوية</span>
                    <span className="font-semibold text-gray-900">90 ج.م</span>
                  </div>
                  <div className="flex justify-between items-center border-b border-gray-100 pb-2 last:border-0 last:pb-0">
                    <span className="text-gray-600">الباقة السنوية</span>
                    <span className="font-semibold text-gray-900">300 ج.م</span>
                  </div>
                </div>
              )}
            </div>

            <div className="flex gap-2">
              <Button 
                onClick={handleSubscribe} 
                disabled={!selectedPlan || loading} 
                className="flex-1 bg-blue-600 hover:bg-blue-700 text-white"
              >
                ارسال طلب الاشتراك
              </Button>
              <Button onClick={() => setStep('initial')} variant="outline">
                رجوع
              </Button>
            </div>
          </div>
        );

      default:
        return (
          <div className="space-y-4 py-4 text-center">
            <p className="text-red-600 font-medium">
              غير مفعلة على حسابك
            </p>
            <p className="text-gray-600 text-sm">
              للاشتراك في خدمة الخطوط، يرجى التواصل مع خدمة العملاء أو تقديم طلب اشتراك.
            </p>
            <div className="flex gap-2 justify-center mt-4">
              <Button onClick={() => setStep('subscribe')} className="w-full bg-blue-600 hover:bg-blue-700">
                اشتراك
              </Button>
              <Button onClick={onClose} variant="outline">
                إغلاق
              </Button>
            </div>
          </div>
        );
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[425px]" dir="rtl">
        <DialogHeader>
          <DialogTitle>خدمة الخطوط</DialogTitle>
          <DialogDescription>
            إدارة اشتراك خدمة الخطوط
          </DialogDescription>
        </DialogHeader>
        {renderContent()}
      </DialogContent>
    </Dialog>
  );
};
